<template>
    <div>
        <el-container>
            <el-header>
                <img class="mlogo" src="https://upload.jianshu.io/users/upload_avatars/21055628/bf6f93c9-8524-4c75-b191-9b6377139f8e.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/120/h/120">
            </el-header>
            <el-main>
                <el-form ref="ruleForm" :rules ="rules" :model="ruleForm" label-width="80px" class="ruleForm">
                    <el-form-item label="用户名" prop="username">
                        <el-input v-model="ruleForm.username"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model="ruleForm.password"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <el-button type="primary" @click="submitForm('ruleForm')">登录</el-button>
                        <el-button @click="resetForm('ruleForm')">取消</el-button>
                    </el-form-item>
                </el-form>
            </el-main>
        </el-container>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                ruleForm: {
                    username: '',
                    password: ''
                },
                rules:{
                    username:[
                        {require:true,message:"请输入用户名",trigger:"blur"},
                        {min:3,message:"长度在3到15个字符",trigger:"blur"},
                    ],
                    password: [
                        {require:true,message:"请输入密码",trigger:"change"},
                        {min:3,message:"长度在3到15个字符",trigger:"blur"},
                    ]
                }
            }
        },
        methods: {
            submitForm(formName) {
                this.$refs[formName].validate((valid)=>{
                    if (valid){
                        // alert("submit")
                        // 将全局的this保存起来
                        const _this= this
                        this.$axios.post("/login",this.ruleForm).then(res=>{
                            // console.log(res.headers)
                            // console.log(res)
                            const jwt = res.headers['authorization']
                            const userInfo = res.data.data
                            // console.log(jwt,userInfo)
                            // 把数据存储到 状态管理中 即全局
                            _this.$store.commit("SET_TOKEN",jwt)
                            _this.$store.commit("SET_USERINFO",userInfo)

                            // console.log(_this.$store.getters.getUser)
                            _this.$router.push("/blogs")
                        })
                    }else {
                        // console.log("error submit");
                        return false
                    }
                });
            },
            resetForm(formName){
                this.$refs[formName].resetFields();
                alert("sdhfal")
            }
        }
    }
</script>

<style scoped>
    .el-header, .el-footer {
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 60px;
    }

    .el-aside {
        background-color: #D3DCE6;
        color: #333;
        text-align: center;
        line-height: 200px;
    }

    .el-main {
        /*background-color: #E9EEF3;*/
        color: #333;
        text-align: center;
        line-height: 160px;
    }

    body > .el-container {
        margin-bottom: 40px;
    }

    .el-container:nth-child(5) .el-aside,
    .el-container:nth-child(6) .el-aside {
        line-height: 260px;
    }

    .el-container:nth-child(7) .el-aside {
        line-height: 320px;
    }
    .mlogo{
        height: 66%;
        margin-top: 10px;
        width: 20%;
    }
    .ruleForm{
        max-width: 500px;
        margin: 0 auto;
    }
</style>